@charset "utf-8";
/*reset*/
html,body{color:#333;font-size:62.5%;-webkit-user-select:none;-webkit-tap-highlight-color: rgba(0,0,0,0);-webkit-tap-highlight-color:transparent;-webkit-user-select:none;-webkit-touch-callout:none;-webkit-touch-callout:none;}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td,hr,button,article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section {-webkit-text-size-adjust:none;/*阻止旋转屏幕时自动调整字体大小*/font-weight:normal;margin:0;padding:0;}
body,button,input,select,textarea {font-family:"Microsoft YaHei","微软雅黑";}
textarea{resize:none;}
input{border:none;outline: 0}/*取消按钮在inphone上的默认样式*/
input[type=text],input[type=button],input[type=submit]{-moz-appearance:none;-webkit-appearance:none;outline:none} 
input::-webkit-input-placeholder{color:#ccc;padding-left:1%;}
input::-moz-input-placeholder{color:#ccc;padding-left:1%;}
textarea::-webkit-input-placeholder{color:#ccc;}
input::-webkit-input-speech-button {display:none}
table {border-collapse:collapse;border-spacing:0;}
th {text-align:inherit;}
fieldset,img {border:none;}
abbr,acronym {border:none;font-variant:normal;}
del {text-decoration:line-through;}
ol,ul {list-style:none;}
caption,th {text-align:left;}
sub,sup {font-size:75%;line-height:0;position:relative;vertical-align:baseline;}
sup {top:-0.5em;}
sub {bottom:-0.25em;}
ins,a,a:hover {text-decoration:none;}
a:focus,*:focus {outline:none;}
*, :before, :after{
    box-sizing: border-box;
}

/*header*/
.lk-header{position:relative;background:#2be;height:44px;line-height:44px;color:#fff;text-align:center;font-size:16px;}
.lk-header span,.lk-header span a{position:absolute;top:2px;display:block;width:40px;height:42px;}.lk-header b{font-weight:normal}
.lk-header span.arrow-left{left:0px;}
.lk-header span.indexIco{right:0px;} 
span.arrow-left img{width:12px;}span.indexIco img{width:20px;}
/*footer*/
.mobile_foot {padding:1.5rem 1rem 2rem;margin-top:1rem;text-align:center;font-size:1.6rem;background: #fff;color:#ccc;position:relative;}.mobile_foot .gotop{position:absolute;right:1rem;top:2rem;}.mobile_foot a {color: #333 !important;}
.mobile_foot .slogan{padding-top:1rem;color:#999;font-size:1.2rem;}

/*loading more*/
.loading-more{text-align:center;background:#fff;display:none;}



.fl {
    float: left; }

.fr {
    float: right; }

.ovh {
    overflow: hidden; }

.hidden {
    display: none !important; }

.text-center {
    text-align: center; }

a {
    color: #666;
    text-decoration: none; }

ul, li {
    list-style: none; }

i {
    font-style: normal; }

[v-cloak] {
    display: none; }

.textOverFlow {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap; }




.wrapper .book-detail {width:100%;margin-top:1rem;background:#fff;overflow:hidden; }

.wrapper .book-detail:first-child {
    margin-top: 0 !important; }
.wrapper .book-detail > .bd {
    padding: 1.2rem 1rem; }


.wrapper .book-detail .book-module {
    position: relative; }
.wrapper .book-detail .book-module .tit {
    color: #333;
    font-size: 1.4rem; }
.wrapper .book-detail .book-module .change-items {
    position: absolute;
    right: 0;
    top: 0.2rem;
    color: #999;
    font-size: 12px; }
.wrapper .book-detail .book-module .book-items {
    margin-top: 1.1rem; }
.book-module .book-items li{ border-top:1px solid #ddd;padding: 5px 0;}
.book-module .book-items li:first-child{border:none;padding:0 0 5px;}
.wrapper .book-detail .book-module .book-items h3 {
    font-size: 1.5rem;
    color: #333;
    line-height: 1.5rem; }
.wrapper .book-detail .book-module .book-items h4 {
    padding-top: 4px;
    font-size: 1.2rem;
    line-height: 1.5rem;
    color: #333; }
.wrapper .book-detail .book-module .book-items p {
    padding-top: 0.8rem;
    color: #999;
    height: 5rem;
    line-height: 1.5rem;
    font-size: 12px; }
.wrapper .book-detail .book-module .layout-l-r a {
    display: block; }
.wrapper .book-detail .book-module .layout-l-r dl {
    display: inline-block;
    padding: 0.7rem 0; }
.wrapper .book-detail .book-module .layout-l-r dl dt {
    float: left; }
.wrapper .book-detail .book-module .layout-l-r dl dd {
    overflow: hidden;
    padding: 0.6rem 0 0 1.4rem; }
.wrapper .book-detail .book-module .layout-t-b {
    overflow: hidden; }
.wrapper .book-detail .book-module .layout-t-b li {
    float: left;
    width: 33.33%;
    text-align: center; }
.wrapper .book-detail .book-module .layout-t-b li a {
    display: inline-block; }
.wrapper .book-detail .book-module .layout-t-b li h3 {
    padding-top: 0.5rem;
    width: 7rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left;
    font-size: 12px; }
.wrapper .book-detail .book-module .more {
    margin-top: 0.7rem; }
.book-items h4 a{color:#2be}

/*margin-top padding-top 0*/
.mg10{margin-top:0rem !important;}
.pd12{padding-top:0rem !important;}

/*灰色背景*/
.gray-bg{background:#f6f6f6;}

//底部导航条
.wrapper .book-action-wrapper {
    position: fixed;
    bottom: 0;
    width: 100%;
}
.wrapper .book-action{
    width: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    border-top: 1px solid #e5e5e5;
    background-color: #fafafa;
    li{
      -webkit-box-flex: 1;
      -webkit-flex: 1;
      flex: 1;
      text-align: center;
      border-right: 1px solid #e5e5e5;
      &:last-child{
        border: none;
      }
      &.home{
        width: 42px;
        -webkit-box-flex: 0;
        -webkit-flex: inherit;
        flex: inherit;
        a{
          padding-top: 6px;
          text-align: center;
        }
        span{
          display: inline-block;
          width: 20px;
          height: 20px;
          background: url(../../img/wx/laikan/bg-book-detail.png) -39px -29px no-repeat;
          -webkit-background-size: auto 60px;
          background-size: auto 60px;
        }
      }
      a{
        display: block;
        width: 100%;
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #666;
        font-size: 15px;
        text-decoration: none;
      }
    }
}

//下拉框样式
.chapter-selectbox{
    position:relative;
    -webkit-box-flex:1.0;-moz-box-flex:1.0;box-flex:1.0;
    border:solid 1px #6cd;border-radius: 3px;
    height:29px;line-height:29px;min-width:100px;margin-right:12px;
    background: url(../../img/wx/laikan/chapteListDown.png) no-repeat right center;
    background-size:20px;
    font-size:14px;
    .selectbox-div{width:90%;line-height:27px;}
    .selectbox-div :after{
        position:absolute;
        content: '';top:0;right:10px;
    }
    .clist_select{
        -webkit-appearance: none;-moz-appearance: none;appearance: none;
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;
        opacity: 0;font-size:12px;
        outline: none;
    }
}

.chapter-selectbox select.clist_select option{
    color:#333;padding:5px 10px;border:none;
}
select.clist_select option[selected]{
    font-weight:bold;
}